import './taskWrapper.css';
import {useSelector} from "react-redux";
import TaskItem from "../TaskItem/TaskItem";
import doesntExist from "../../assets/img/doesntExist.png";
import CrudNotification from "../Notifications/CrudNotification";

const TaskWrapper = (props) => {
    const {removeTask, editTask, completeTask, alertType, activeTasks} = props;
    const {taskList, isShowCreateAlert} = useSelector(state => state.taskReducer);

    return (
        <div className="task-wrapper">
            {isShowCreateAlert && <CrudNotification alertType={alertType}/>}
            {/*<h1>TASKS</h1>*/}
            {
                taskList
                &&
                // eslint-disable-next-line array-callback-return
                taskList.map((task) => {
                    if (task.id && !task.status) {
                        return (
                            <div key={task.id}>
                                <TaskItem
                                    completeHandler={completeTask}
                                    editTaskHandler={editTask}
                                    deleteTaskHandler={removeTask}
                                    title={task.title}
                                    id={task.id}
                                />
                            </div>
                        )
                    }
                })
            }
            {!activeTasks &&
                <div className="flex justify-center mt-20">
                    <img
                        style={{maxWidth: '50%'}}
                        className="flex-auto"
                        src={doesntExist}
                        alt="doesnt exists"
                    />
                </div>
            }
        </div>
    )
}

export default TaskWrapper;
