import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import Form from "./components/Form/Form";
import {useSelector, useDispatch} from "react-redux";
import TaskWrapper from "./components/TaskWrapper/TaskWrapper";
import Storage from "./assets/utils/storage";
import {addTask, createAlertHandler, createModeHandler, deleteTask, editModeHandler} from "./store/taskSlice";
import {useEffect, useState} from "react";

function App() {
    const dispatch = useDispatch();
    const {isCreateMode, isEditMode, taskList} = useSelector(state => state.taskReducer);
    const [newTaskData, setNewTaskData] = useState('');
    const [existingTaskData, setExistingTaskData] = useState('');
    const [id, setId] = useState(0);
    const [alertType, setAlertType] = useState('');
    const [activeTasksLength, setActiveTasksLength] = useState(0);

    const createTaskHandler = todoItem => {
        Storage.setNewTask(todoItem);
        dispatch(addTask(Storage.getData()));
        setAlertType('create');
        dispatch(createAlertHandler(true));
    }
    const editStoreHandler = () => {
        Storage.editTask(id, existingTaskData);
        setAlertType('edit');
        dispatch(createAlertHandler(true));
    }
    const editTaskHandler = e => {
        dispatch(editModeHandler(!isEditMode));
        const taskId = +e.target.id;
        const {title} = Storage.findTask(taskId);
        setExistingTaskData(title);
        setId(taskId);
    }
    const deleteTaskHandler = e => {
        e.stopPropagation();
        // eslint-disable-next-line no-restricted-globals
        if (confirm('Do you want to delete this task?')) {
            dispatch(deleteTask(+e.target.id));
            Storage.deleteTaskHandler(+e.target.id);
            setAlertType('delete');
            dispatch(createAlertHandler(true));
        }
    }

    const completeTaskHandler = e => {
        const id = +e.target.parentNode.id;
        if (id !== 0) {
            // eslint-disable-next-line no-restricted-globals
            if (confirm('Did you complete this task?')) {
                Storage.completeTaskHandler(id);
                dispatch(addTask(Storage.getData()))
            }
        }
    }


    const closeFormHandler = () => {
        dispatch(editModeHandler(false));
        dispatch(createModeHandler(false));
    }

    useEffect(() => {
        dispatch(addTask(Storage.getData()))
    }, [])

    useEffect(() => {
        if (taskList) {
            const activeTasks = taskList.filter((task) => task.status === false).length;
            setActiveTasksLength(activeTasks);
        }
    }, [taskList])

    return (
        <div className="App ">
            <Header tasks={taskList}/>

            <TaskWrapper
                editTask={editTaskHandler}
                removeTask={deleteTaskHandler}
                completeTask={completeTaskHandler}
                activeTasks={activeTasksLength}
                alertType={alertType}/>

            {isCreateMode && <Form
                newTaskData={newTaskData}
                onChangeNewTaskData={setNewTaskData}
                createHandler={createTaskHandler}

                closeForm={closeFormHandler}/>}
            {isEditMode && <Form
                storeHandler={editStoreHandler}
                editingTaskData={existingTaskData}
                onChangeEditingTaskData={setExistingTaskData}
                closeForm={closeFormHandler}/>}

            <Footer/>
        </div>
    );
}

export default App;
