import './crudNotification.css';
import {useEffect, useState} from 'react';
import {createAlertHandler} from "../../store/taskSlice";
import {useDispatch} from "react-redux";

const SuccessAlert = ({alertType}) => {

    const [visibility, setVisibility] = useState('alert-wrapper');
    const dispatch = useDispatch();

    const generateAlert = (alertType) => {

        // eslint-disable-next-line default-case
        switch (alertType) {
            case 'create':
                return (
                    <div className="bg-indigo-900 text-center py-4 lg:px-4">
                        <div
                            className="p-2 bg-indigo-800 items-center text-indigo-100 leading-none lg:rounded-full flex lg:inline-flex"
                            role="alert">
                    <span
                        className="flex rounded-full bg-indigo-500 uppercase px-2 py-1 text-xs font-bold mr-3">Created</span>
                            <span
                                className="font-semibold mr-2 text-left flex-auto">The task was successfully created</span>
                        </div>
                    </div>
                )
            case 'edit':
                return (
                    <div className="bg-orange-500 text-center py-4 lg:px-4">
                        <div
                            className="p-2 bg-orange-600 items-center text-indigo-100 leading-none lg:rounded-full flex lg:inline-flex"
                            role="alert">
                    <span
                        className="flex rounded-full bg-orange-700 uppercase px-2 py-1 text-xs font-bold mr-3">Edited</span>
                            <span className="font-semibold mr-2 text-left flex-auto">The task was successfully edited</span>
                        </div>
                    </div>
                )
            case 'delete':
                return (
                    <div className="bg-red-900 text-center py-4 lg:px-4">
                        <div
                            className="p-2 bg-red-800 items-center text-indigo-100 leading-none lg:rounded-full flex lg:inline-flex"
                            role="alert">
                    <span
                        className="flex rounded-full bg-red-500 uppercase px-2 py-1 text-xs font-bold mr-3">Deleted</span>
                            <span
                                className="font-semibold mr-2 text-left flex-auto">The task was successfully deleted</span>
                        </div>
                    </div>
                )
        }

    }

    useEffect(() => {
        setTimeout(() => {
            setVisibility('alert-wrapper-hidden');
        }, 3000);
        setTimeout(() => {
            dispatch(createAlertHandler(false))
        }, 3800);
    }, []);

    return (
        <div className={visibility}>
            {generateAlert(alertType)}
        </div>

    );
};

export default SuccessAlert;
