class Storage {
    #DB_KEY_TASK_LIST = 'TASK_LIST';
    #id = 1;

    #getId() {
        return this.#hasData() ? 1 + this.getData().at(0).id : this.#id;
    }

    #getFilteredData(id) {
        return this.getData().filter(task => task.id !== id);
    }

    #setUpdatedData(data) {
        return localStorage.setItem(this.#DB_KEY_TASK_LIST, JSON.stringify(data))
    }

    #taskStatusHandler(status, storedData, currentTask, taskIndex) {
        currentTask.status = status;
        storedData.splice(taskIndex, 1, currentTask);
        return this.#setUpdatedData(storedData);
    }

    #hasData() {
        return (!!this.getData() && !!this.getData().length);
    }

    findTask(id) {
        return this.getData().find(task => task.id === id)
    }

    getData() {
        return JSON.parse(localStorage.getItem(this.#DB_KEY_TASK_LIST));
    }

    setNewTask(title) {
        if (typeof title !== 'string') throw new Error('Provided title should be in a String type')
        const taskData =
            [{
                title: title,
                id: this.#getId(),
                status: false
            }];

        if (!this.#hasData()) {
            localStorage.setItem(this.#DB_KEY_TASK_LIST, JSON.stringify(taskData))
            return taskData
        }

        const storedData = this.getData();
        const updatedData = [...taskData, ...storedData];
        localStorage.setItem(this.#DB_KEY_TASK_LIST, JSON.stringify(updatedData));
        return updatedData;
    }

    editTask(id, title) {
        const storedData = this.getData();
        const currentTask = this.findTask(id);
        currentTask.title = title;
        const taskIndex = storedData.findIndex(task => task.id === id);
        storedData.splice(taskIndex, 1, currentTask);
        this.#setUpdatedData(storedData);
    }

    deleteTaskHandler(id) {
        const filteredTasks = this.#getFilteredData(id);
        this.#setUpdatedData(filteredTasks);
    }

    completeTaskHandler(id) {
        const storedData = this.getData();
        const currentTask = this.findTask(id);
        const taskIndex = storedData.findIndex(task => task.id === id);

        currentTask.status
            ?
            this.#taskStatusHandler(false, storedData, currentTask, taskIndex)
            :
            this.#taskStatusHandler(true, storedData, currentTask, taskIndex)
    }
}

export default new Storage();
